
.home {
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	gap: 30px;
}
.preview-image {
	height: 100%;
	width: 100%;
}
.fill {
	object-fit: fill;
}
.cover {
	object-fit: cover;
}
.contain {
	object-fit: contain;
}
.none {
	object-fit: none;
}
.upload-demo {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.el-carousel {
	height: 100%;
	&__container {
		height: 100% !important;
	}
}
.el-input {
	font-size: 16px !important;
}
.el-upload {
	&__tip {
		text-align: center;
	}
}
.el-upload-dragger {
	@media (max-width: 649px) {
		width: 300px !important;
	}
}
.el-upload-list {
	padding-top: 20px !important;
	width: 300px;
}
.el-dialog {
	&__header {
		padding: 0 !important;
	}
	&__body {
		height: 90%;
		padding: 40px 30px !important;
		box-sizing: border-box;
	}
	&__footer {
		text-align: center !important;
	}
}
.none-events {
	pointer-events: none;
}
